.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    height: 30vh;
    margin-top: 8rem;
    margin-bottom: 8rem;
}

.achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.circle{
    width: 5rem;
    height: 5rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
}

.circle::before{
    border-radius: 100%;
    content: "";
    background-image: linear-gradient(to bottom,#87e6fb 0%, #ffc05c 100%);
top: -8px;
left: -8px;
bottom: -8px;
right: -8px;
position: absolute;
z-index: -1;
box-shadow: var(--boxShadow);

}

.achievement span:nth-of-type(2)
{
    color: var(--orange);
}
@media screen and (max-width:480px) {

   .experience{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    grid-template-rows: auto;
   }
   .circle{
    width: 8rem;
    height: 8rem;
    margin-top: -4rem;
}

}
